<template>
  <div id="app">
    <div>
    <div class="container mt-3">
      <div class="mt-3">
        <h3><i class="fas fa-clipboard"></i> Pedidos</h3>
      </div>
      <table-sortable class="mt-3" :items="items" :fields="fields" :rutaNuevo="rutaNuevo"
        :permisoEscritura="permisoEscritura" @show-modal="showModal"></table-sortable>
    </div>
  </div>
  </div>
</template>

<script>
  import TableSortable from "@/components/TableSortable";
  import servicio from "@/services/servicio-agentes";
  export default {
    data() {
      return {
        data: {
          operadorCR: {
            nombreCompleto: "",
          },
          operadorBB: {
            nombreCompleto: "",
          },
        },
        modalShow: false,
        rutaNuevo: "nota-de-remision/registro/",
        items: [],
        fields: [
          {
            key: "folioPedido",
            label: "Folio",
            sortable: true,
            class: "text-center",
          },
          {
            key: "cliente",
            label: "Cliente",
            sortable: true,
            class: "text-center",
          },
          { key: "obra", label: "Obra", sortable: true, class: "text-center" },
          {
            key: "estatus",
            label: "Estatus",
            sortable: true,
            class: "text-center"
          },
          {
            key: "fechaSalida",
            label: "Fecha",
            sortable: true,
            class: "text-center",
          },
          { key: "cantNotasRemision", label: "NR", class: "text-center" },
          { key: "descargar", label: "Descarga", class: "text-center" },
        ],
        permisoEscritura: false,
      };
    },
    components: {
      TableSortable,
    },
    async mounted() {
      await this.ObtenerPedidos();
    },
    methods: {
      async ObtenerPedidos() {
        this.$loading(true);
        await servicio
          .obtenerPedidosAgente()
          .then((r) => {
            this.items = r.data;
            console.log(r.data);
            this.$loading(false);
          })
          .catch((error) => {
            this.$loading(false);
            console.error(error);
          });
      },
      async showModal(data) {
        let r = await servicio.obtenerDetalleNotasRemisionAgente(
          data.item.idNotasRemisionEnc
        );
        this.data = data.item;
        if (r.data.length > 1) {
          this.data.bombeable = true;
          this.data.operadorCR = r.data[0];
          this.data.operadorBB = r.data[1];
        } else {
          this.data.bombeable = false;
          this.data.operadorCR = r.data[0];
        }
        this.modalShow = true;
      },
    },
  };
</script>
<style scoped></style>